import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import "./TopMenu.css";

const TopMenu = () => {
    const [role, setRole] = useState(null); // role  
    const navigate = useNavigate();

    const fetchRoleFromToken = () => {
        const token = localStorage.getItem("token");
        if (token) {
            try {
                console.log(" :", token);
                const payload = JSON.parse(atob(token.split(".")[1]));
                console.log("  Payload:", payload);
                setRole(parseInt(payload.role, 10)); // role  
            } catch (e) {
                console.error("  :", e);
            }
        } else {
            console.log(" ");
            setRole(null); //   role 
        }
    };

    useEffect(() => {
        console.log("TopMenu    useEffect ");
        fetchRoleFromToken();

        const handleLoginEvent = () => {
            console.log("  , fetchRoleFromToken ");
            fetchRoleFromToken(); // role  
        };

        window.addEventListener("login", handleLoginEvent);

        return () => {
            window.removeEventListener("login", handleLoginEvent);
        };
    }, []);

    const handleLogout = () => {
        localStorage.removeItem("token");
        setRole(null);
        alert(".");
        navigate("/");
    };

    return (
        <nav className="top_nav">
            <div className="top_first">
                <div className="top_menu">
                    <ul>
                        <li>
                            <Link className="logo" to={"/"}></Link>
                        </li>
                        <li>
                            <input type="text" placeholder=" " className="search_box" />
                        </li>
                    </ul>
                </div>
                <div className="top_menu_btn">
                    {!role && (
                        <>
                            <Link to="/sign_up">
                                <button className="menu_btn"></button>
                            </Link>
                            <Link to="/login">
                                <button className="menu_btn"></button>
                            </Link>
                        </>
                    )}
                    {role === 1 && (
                        <>
                            <Link to="/mypage">
                                <button className="menu_btn"></button>
                            </Link>
                            <Link to="/userinfo">
                                <button className="menu_btn"></button>
                            </Link>
                            <button className="menu_btn" onClick={handleLogout}></button>
                        </>
                    )}
                    {role === 0 && (
                        <>
                            <Link to="/admin">
                                <button className="menu_btn"></button>
                            </Link>
                            <Link to="/userinfo">
                                <button className="menu_btn"></button>
                            </Link>
                            <button className="menu_btn" onClick={handleLogout}></button>
                        </>
                    )}
                </div>
            </div>
            <div className="second_menu">
                <ul>
                    <li>
                        <Link to={"/about"}> </Link>
                    </li>
                    <li>
                        <Link to={"/find_store"}> </Link>
                    </li>
                    <li>
                        <Link to={"/ranking"}></Link>
                    </li>
                    <li>
                        <Link to={"/pick_crew"}> </Link>
                    </li>
                </ul>
            </div>
        </nav>
    );
};

export default TopMenu;