import React, { useState } from "react";
import DiningLayout from '../layouts/DiningLayout'
import "../css/Admin_Report.css";

const Admin_Report = () => {
    //   
    const members = Array.from({ length: 100 }, (_, i) => ({
        id: i + 1,
        nickname: `${i + 1}`,
        reviewCount: Math.floor(Math.random() * 10),
        commentCount: Math.floor(Math.random() * 10),
        crewCount: Math.floor(Math.random() * 10),
        date: `2024-12-${String(17 + (i % 14)).padStart(2, "0")}`,
    }));

    //  
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 10;

    //    
    const currentData = members.slice(
        (currentPage - 1) * itemsPerPage,
        currentPage * itemsPerPage
    );

    //   
    const totalPages = Math.ceil(members.length / itemsPerPage);

    return (
        <DiningLayout>
            <div className="adminReport-table-container">
                <h1> </h1>
                <div className="table-controls">
                    <input type="text" placeholder="" className="search-input"/>
                    <button className="control-button"></button>
                    <button className="control-button"></button>
                </div>
                <table className="member-table">
                    <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th> </th>
                        <th> </th>
                        <th> </th>
                        <th> </th>
                        <th> </th>
                        <th> </th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    {currentData.map((member) => (
                        <tr key={member.id}>
                            <td>
                                <input type="checkbox"/>
                            </td>
                            <td>{member.id}</td>
                            <td>{member.nickname}</td>
                            <td>
                                <button> </button>
                            </td>
                            <td>
                                <button> </button>
                            </td>
                            <td>
                                <button> </button>
                            </td>
                            <td>{member.reviewCount}</td>
                            <td>{member.commentCount}</td>
                            <td>{member.crewCount}</td>
                            <td>{member.date}</td>
                        </tr>
                    ))}
                    </tbody>
                </table>
                <div className="pagination">
                    <button
                        onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
                        disabled={currentPage === 1}
                    >
                        &lt;
                    </button>
                    {Array.from({length: totalPages}, (_, i) => (
                        <button
                            key={i + 1}
                            onClick={() => setCurrentPage(i + 1)}
                            className={currentPage === i + 1 ? "active" : ""}
                        >
                            {i + 1}
                        </button>
                    ))}
                    <button
                        onClick={() => setCurrentPage((prev) => Math.min(prev + 1, totalPages))}
                        disabled={currentPage === totalPages}
                    >
                        &gt;
                    </button>
                </div>
            </div>
        </DiningLayout>
    );
};

export default Admin_Report;
